.site-header {
  background-color: #242424;
  background: #fff;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.05);
}

.site-header-wrap {
  padding: 20px;
  flex-wrap: wrap;
  @include media-breakpoint-down(lg) {
    padding: 20px 15px;
  }
}

.site-branding,
.header-group {
  width: 19%;
  @include media-breakpoint-down(md) {
    width: 45%;
  }
}

